import { useState, useEffect } from "react";
import {
  Box,
  Container,
  Paper,
  Typography,
  Button,
  TextField,
  Divider,
  InputAdornment,
  IconButton,
} from "@mui/material";
import { Phone, WhatsApp, Visibility, VisibilityOff } from "@mui/icons-material";
import KeyIcon from "@mui/icons-material/Key";
import { MuiTelInput, matchIsValidTel } from "mui-tel-input";
import { useNavigate, Link } from "react-router-dom";
import { login } from "../../../api/user_account";
import Notification from "./components/Notification";
import GoogleIcon from "./components/GoogleIcon";
import { initGoogleAuth } from "../../../utils/goolgeAuth";
import { useDispatch, useSelector } from "react-redux";
import { redux_login } from "../../../store/redux/authReducer";
export default function LoginPage() {
  const [phoneNumber, setPhoneNumber] = useState("");
  const [password, setPassword] = useState("");
  const [step, setStep] = useState("phone");
  const navigate = useNavigate();
  const [showNotification, setShowNotification] = useState(false);
  const [error, setError] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const  dispatch = useDispatch();
  const links = [
    { path: "/terms", name: "Terms of Service" },
    { path: "/privacy", name: "Privacy Policy" },
  ];
  const handleLoginSuccess = () => {
    dispatch(redux_login());
    setShowNotification(true);
    setTimeout(() => {
      navigate("/dashboard");
    }, 1500);
  };
  const { handleGoogleLogin, loadGoogleScript } = initGoogleAuth(
    process.env.REACT_APP_GOOGLE_CLIENT_ID,
    handleLoginSuccess
  );
  useEffect(() => {
    const cleanup = loadGoogleScript();
    return cleanup;
  }, [loadGoogleScript]);
  const handlePhoneSubmit = async (e) => {
    e.preventDefault();
    setError("");
    console.log("Phone:", phoneNumber);
    if (!matchIsValidTel(phoneNumber)) {
      setError("Please enter a valid phone number");
      return;
    }

    try {
      // await verify_phone({"phoneNumber":phoneNumber});
      setStep("password");
    } catch (error) {
      alert("Failed to send phone number.Please try again.");
    }
  };
  const validatePassword = (password) => {
    // Regular expression:  "The password must contain one capital letter, one special character from !@#$%^&*, and be longer than 6 characters."
    const passwordRegex = /^(?=.*[A-Z])(?=.*[!@#$%^&*])(?=.{7,})/;
    return passwordRegex.test(password);
  };

  const handlePasswordSubmit = async (e) => {
    e.preventDefault();

    if (!validatePassword(password)) {
      setError(
        "The password must contain one capital letter, one special character from !@#$%^&*, and be longer than 6 characters."
      );
      return;
    }
    setError("");
    try {
      const response = await login({
        phoneNumber: phoneNumber,
        password: password,
      });
      if (response.status === 200) {
        handleLoginSuccess();
      } else {
        setShowNotification(true);
        setError(
          response.message ||
            "Login failed.Please check your password and try again."
        ); // Set error message
      }
    } catch (error) {
      setShowNotification(true);
      setError(error || "Login failed.Please try again."); // Set error message
    }
  };

  return (
      <Box
        sx={{
          minHeight: "100vh",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          padding: 2,
        }}
      >
        <Container maxWidth="sm">
          <Paper
            elevation={24}
            sx={{
              p: 4,
              backgroundColor: "background.paper",
              backdropFilter: "blur(10px)",
              border: "1px solid rgba(255, 255, 255, 0.1)",
              borderRadius: 2,
            }}
          >
            <Typography
              variant="h4"
              component="h1"
              align="center"
              sx={{
                mb: 5,
                color: "primary.main",
                fontWeight: 700,
                letterSpacing: "0em",
                fontSize: { xs: "2.5rem", sm: "3rem", md: "4.5rem" },
                lineHeight: 1.2,
              }}
            >
              rife.
            </Typography>

            <Button
              fullWidth
              variant="outlined"
              onClick={handleGoogleLogin}
              sx={{
                mb: 3,
                backgroundColor: "rgba(255, 255, 255, 0.05)",
                borderColor: "rgba(255, 255, 255, 0.1)",
                color: "white",
                "&:hover": {
                  backgroundColor: "rgba(255, 255, 255, 0.1)",
                  borderColor: "rgba(255, 255, 255, 0.2)",
                },
                textTransform: "none",
                fontSize: { xs: "0.8rem", sm: "0.8rem", md: "1.2rem" },
                padding: "12px 16px",
                fontWeight: "bold",
                height: "48px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <GoogleIcon />
              Continue with Google
            </Button>
            <Divider sx={{ mb: 3 }}>
              <Typography variant="body2" color="text.secondary">
                or
              </Typography>
            </Divider>

            {step === "phone" ? (
              <form onSubmit={handlePhoneSubmit}>
                <MuiTelInput
                  defaultCountry="GB" // Default country set to UK
                  value={phoneNumber}
                  onChange={setPhoneNumber}
                  fullWidth
                  label="Phone Number"
                  variant="outlined"
                  sx={{ mb: 2 }}
                  error={!!error}
                  helperText={error}
                />
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  startIcon={<Phone />}
                  sx={{
                    textTransform: "none",
                    fontSize: { xs: "0.8rem", sm: "0.8rem", md: "1.2rem" },
                    padding: "10px 10px ",
                    fontWeight: "bold",
                  }}
                >
                  Continue with Phone
                </Button>
              </form>
            ) : (
              <form onSubmit={handlePasswordSubmit}>
                <TextField
                  fullWidth
                  label="Enter Password"
                  variant="outlined"
                  value={password}
                  type={showPassword ? "text" : "password"}
                  onChange={(e) => setPassword(e.target.value)}
                  sx={{ mb: 2 }}
                  error={!!error}
                  helperText={error}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <KeyIcon />
                      </InputAdornment>
                    ),
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="VisibilityOff"
                          onClick={() => {
                            setShowPassword(
                              (prevShowPassword) => !prevShowPassword
                            );
                          }}
                          edge="end"
                        >
                          {showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    ),
                  }}
                />
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{
                    textTransform: "none",
                    fontSize: { xs: "0.8rem", sm: "0.8rem", md: "1.2rem" },
                    padding: "10px 10px ",
                    fontWeight: "bold",
                  }}
                >
                 Sign in / Sign up
                </Button>
              </form>
            )}

            <Button
              fullWidth
              variant="outlined"
              startIcon={<WhatsApp style={{ color: "green" }} />}
              sx={{
                textTransform: "none",
                fontSize: { xs: "0.8rem", sm: "0.8rem", md: "1.2rem" },
                padding: { xs: "8px 8px", sm: "10px 10px", md: "12px 12px" },
                fontWeight: "bold",
                marginTop: "16px",
              }}
              href="whatsapp://send?text=Discover Rife, an amazing way to make payments! https://rifeapp.github.io/io/ "
            >
              Share via WhatsApp
            </Button>

            <Typography
              variant="body2"
              align="center"
              sx={{
                mt: 3,
                color: "text.secondary",
                fontSize: "0.875rem",
              }}
            >
                By continuing, you agree to our{" "}
               {links.map((link, index) => (
                  <>
                  <Link to={link.path} 
                  style={{ color: "#2DD4BF" }}
                  sx={{
                    textDecoration: "none",
                    "&:hover": {
                      textDecoration: "underline",
                    },
                  }}>{link.name} </Link>
                  {index < links.length - 1 && "   and    "}
                  </>
             ))}

            </Typography>
          </Paper>
          {showNotification && (
            <Notification
              message="Login successful! "
              open={showNotification}
              onClose={() => setShowNotification(false)}
            />
          )}
        </Container>
      </Box>
  );
}
