import PropTypes from 'prop-types';
import { Box, Paper, Typography } from "@mui/material";
import { Edit, Add, ChevronRight } from "@mui/icons-material";
const SettingSection = ({ icon: Icon, title, value, action, onClick }) => (
  <Paper
    sx={{
      p: 3,
      mb: 2,
      backgroundColor: "background.paper",
      backdropFilter: "blur(10px)",
      border: "1px solid rgba(255, 255, 255, 0.1)",
      borderRadius: 2,
      "&:hover": {
        backgroundColor: "rgba(255, 255, 255, 0.05)",
      },
    }}
  >
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        cursor: "pointer",
      }}
      onClick={onClick}
    >
      <Box sx={{ display: "flex", alignItems: "center", gap: 2 }}>
        <Icon sx={{ color: "primary.main", fontSize: 24 }} />
        <Box>
          <Typography
            variant="subtitle1"
            sx={{ color: "text.primary", mb: 0.5 }}
          >
            {title}
          </Typography>
          <Typography variant="body2" sx={{ color: "text.secondary" }}>
            {value}
          </Typography>
        </Box>
      </Box>
      {action === "add" ? (
        <Add sx={{ color: "primary.main" }} />
      ) : action === "edit" ? (
        <Edit sx={{ color: "primary.main" }} />
      ) : (
        <ChevronRight sx={{ color: "primary.main" }} />
      )}
    </Box>
  </Paper>
);

SettingSection.propTypes = {
    icon: PropTypes.node.isRequired,     // React 
    title: PropTypes.string.isRequired,  // 
    value: PropTypes.string.isRequired,  // 
    action: PropTypes.node,              //  React 
    onClick: PropTypes.func              // 
  };
  
  SettingSection.defaultProps = {
    action: null,
    onClick: () => {}
  };
export default SettingSection;
