import { useState } from 'react';
import { 
  Box,
  Container,

  Typography,

  Divider,

} from '@mui/material';
import { CreditCard } from '@mui/icons-material';
import SettingSection from './components/SettingSection';
import AccountSection from './components/AccountSection';
import { useNavigate } from 'react-router-dom';



export default function SettingsPage() {
  const [cards] = useState([
    { id: 1, last4: '4242', type: 'Visa' },
    { id: 2, last4: '8888', type: 'Mastercard' },
  ]);
  const navigate=useNavigate();

  return (
      <Box
        sx={{
          minHeight: "100vh",
          display: "flex",
          alignItems: "flex-start",
          justifyContent: "center",
          padding: 2,
          width: "100%",
          maxWidth: "1200px",
          margin: "0 auto",
        }}
      >
        <Container maxWidth="md">
          <Typography
            variant="h4"
            component="h1"
            sx={{
              mb: 4,
              color: "primary.main",
              fontWeight: 700,
              letterSpacing: "0em",
              fontSize: { xs: '2rem', sm: '2.5rem', md: '3rem' },
              lineHeight: 1.2,
            }}
          >
            Settings
          </Typography>

          <Typography
            variant="h6"
            sx={{
              mb: 2,
              color: "text.primary",
              fontWeight: 600,
            }}
          >
            Payment Methods
          </Typography>

          {cards.map((card) => (
            <SettingSection
              key={card.id}
              icon={CreditCard}
              title={`${card.type} ending in ${card.last4}`}
              value="Default payment method"
              action="edit"
              onClick={() => console.log('Edit card', card.id)}
            />
          ))}

          <SettingSection
            icon={CreditCard}
            title="Add new card"
            value="Add a new payment method"
            action="add"
            onClick={() => navigate('/dashboard/add-card')}
          />

          <Divider sx={{ my: 4, borderColor: 'rgba(255, 255, 255, 0.1)' }} />
          <AccountSection />
        </Container>
      </Box>

  );
}

