import React, { useState } from 'react';
import {
  Box,
  Typography,
  IconButton,
  Avatar,
  Button,
  Menu,
  MenuItem,
  ListItemIcon,
  ListItemText,
  Divider,
} from '@mui/material';
import { WhatsApp } from '@mui/icons-material';
import { Bell, User, Settings, MessageCircle, HelpCircle, LogOut } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { redux_logout } from '../../../store/redux/authReducer';
export default function Header() {
  const navigate = useNavigate();
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const dispatch = useDispatch();

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  
  const handleLogout = () => {
    dispatch(redux_logout());
  }

  return (
    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
      <Typography
        variant="h4"
        sx={{
          color: '#2DD4BF',
          fontWeight: 600,
          letterSpacing: '-0.02em',
        }}
      >
        rife.
      </Typography>
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 3 }}>
        <Button
          variant="contained"
          startIcon={<WhatsApp style={{ color: "green" }} />}
          sx={{
            backgroundColor: '#2DD4BF',
            '&:hover': {
              backgroundColor: '#26b5a3',
            },
            textTransform: 'none',
            px: 3,
          }}
          href="whatsapp://send?text=Discover Rife, an amazing way to make payments! https://rifeapp.github.io/io/ "
        >
          Share via WhatsApp
        </Button>
        <IconButton
          sx={{
            backgroundColor: 'rgba(255, 255, 255, 0.05)',
            '&:hover': { backgroundColor: 'rgba(255, 255, 255, 0.1)' },
          }}
        >
          <Bell size={20} color="white" />
        </IconButton>
        <Avatar
          onClick={handleClick}
          aria-controls={open ? 'account-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
          sx={{
            bgcolor: '#2DD4BF',
            width: 32,
            height: 32,
            fontSize: '0.875rem',
            cursor: 'pointer',
            '&:hover': {
              opacity: 0.8,
            },
          }}
        >
          JD
        </Avatar>
        <Menu
          id="account-menu"
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          onClick={handleClose}
          PaperProps={{
            elevation: 0,
            sx: {
              overflow: 'visible',
              filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
              mt: 1.5,
              backgroundColor: 'rgba(26, 27, 30, 0.95)',
              backdropFilter: 'blur(10px)',
              border: '1px solid rgba(255, 255, 255, 0.1)',
              borderRadius: 2,
              minWidth: 200,
              '& .MuiMenuItem-root': {
                py: 1.5,
                px: 2,
                color: 'white',
                '&:hover': {
                  backgroundColor: 'rgba(45, 212, 191, 0.1)',
                },
              },
            },
          }}
          transformOrigin={{ horizontal: 'right', vertical: 'top' }}
          anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
        >
          <MenuItem onClick={() => navigate('/settings')}>
            <ListItemIcon>
              <Settings size={18} color="#2DD4BF" />
            </ListItemIcon>
            <ListItemText>Settings</ListItemText>
          </MenuItem>
          <MenuItem onClick={() => navigate('/support')}>
            <ListItemIcon>
              <MessageCircle size={18} color="#2DD4BF" />
            </ListItemIcon>
            <ListItemText>Support</ListItemText>
          </MenuItem>
          <MenuItem onClick={() => navigate('/help')}>
            <ListItemIcon>
              <HelpCircle size={18} color="#2DD4BF" />
            </ListItemIcon>
            <ListItemText>Help Center</ListItemText>
          </MenuItem>
          <Divider sx={{ borderColor: 'rgba(255, 255, 255, 0.1)' }} />
          <MenuItem onClick={handleLogout}>
            <ListItemIcon>
              <LogOut size={18} color="#ef4444" />
            </ListItemIcon>
            <ListItemText sx={{ color: '#ef4444' }}>Sign out</ListItemText>
          </MenuItem>
        </Menu>
      </Box>
    </Box>
  );
}

