import React from 'react';
import { Box, List, ListItem, ListItemIcon, ListItemText, Paper } from '@mui/material';
import { Home, CreditCard, DollarSign } from 'lucide-react';
import { Link, useLocation } from 'react-router-dom';

const menuItems = [
  { text: 'Home', icon: Home, path: '/dashboard' },
  { text: 'Payments', icon: DollarSign, path: '/payments' },
  { text: 'Cards', icon: CreditCard, path: '/cards' },
];

export default function Sidebar() {
  const location = useLocation();

  return (
    <Paper
      sx={{
        width: 240,
        backgroundColor: 'rgba(26, 27, 30, 0.8)',
        backdropFilter: 'blur(10px)',
        border: '1px solid rgba(255, 255, 255, 0.1)',
        borderRadius: 0,
        height: '100vh',
        position: 'fixed',
        top: 0,
        left: 0,
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      <Box sx={{ p: 2, borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
        <img src="/logo.png" alt="Rife Logo" style={{ width: 100 }} />
      </Box>
      <List sx={{ flexGrow: 1, pt: 2 }}>
        {menuItems.map((item) => (
          <ListItem
            button
            key={item.text}
            component={Link}
            to={item.path}
            sx={{
              color: 'white',
              '&:hover': {
                backgroundColor: 'rgba(45, 212, 191, 0.1)',
              },
              ...(location.pathname === item.path && {
                backgroundColor: 'rgba(45, 212, 191, 0.2)',
                '&:hover': {
                  backgroundColor: 'rgba(45, 212, 191, 0.3)',
                },
              }),
            }}
          >
            <ListItemIcon sx={{ color: '#2DD4BF' }}>
              <item.icon size={24} />
            </ListItemIcon>
            <ListItemText primary={item.text} />
          </ListItem>
        ))}
      </List>
    </Paper>
  );
}
